.title{font-family:"Avenir Next", "Muli", sans-serif;text-transform:uppercase}.section{display:flex;align-items:center;padding:3rem}.section .left{align-self:flex-start;margin-right:5%}.section .left+img{min-width:40%}.section img.main{width:100%;margin-bottom:1rem}.button{color:#eee;background-color:#21618c;text-transform:uppercase;text-align:center;margin:1rem 0;border-radius:0.5rem}.legitRipple-ripple{transition-duration:.3s}.legitRipple-ripple:nth-of-type(1){background-color:#5cabde}.legitRipple-ripple:nth-of-type(3){background-color:#86c1e9}.legitRipple-ripple:nth-of-type(5){background-color:#afdaf5}.legitRipple-ripple:nth-of-type(7){background-color:#d6ecfb}.legitRipple-ripple:nth-of-type(2n){background-color:#f5f5f5}#loader{position:fixed;z-index:100000;top:calc(50% - 76px);left:calc(50% - 59px);opacity:0}#loader div{position:absolute;border:4px solid #2e86c1;opacity:1;border-radius:50%;-webkit-animation:loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}#loader div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes loader{0%{top:56px;left:56px;width:0;height:0;opacity:1}100%{top:-1px;left:-1px;width:116px;height:116px;opacity:0}}@keyframes loader{0%{top:56px;left:56px;width:0;height:0;opacity:1}100%{top:-1px;left:-1px;width:116px;height:116px;opacity:0}}
