main{padding-top:76px}#nav{position:fixed;background:#f6f6f6;width:calc(100% - 2.4rem);height:76px;box-shadow:2px 2px 5px #aaa;z-index:99;display:inline-block;overflow:hidden;display:flex;align-items:center;justify-content:space-between;text-transform:uppercase;list-style-type:none}#nav *{margin:0}#nav #logo-text{display:flex;flex-direction:column;justify-content:space-between;margin-left:-1.2rem}#nav #logo-text .title{font-size:2.5em;letter-spacing:5px;line-height:0.8;font-weight:700}#nav #logo-text #blurb{font-size:1.063em}#nav li{margin:0;padding:0;float:left}#nav #sidenav-trigger{display:none;position:absolute;left:1.5rem;font-size:2em}#links a{transition:background-color .3s;display:block;padding:0 2vw;line-height:76px}#links a:hover{background-color:rgba(0,0,0,0.1)}#sidenav{width:0;position:fixed;z-index:100;padding:0;max-width:300px;height:100vh;background-color:#f5f5f5;list-style-type:none;overflow:scroll}#sidenav #close{text-align:right;font-size:2em;margin-left:calc(100% - 1em)}#backdrop{display:none;background-color:rgba(51,51,51,0.2);position:fixed;top:0;right:0;bottom:0;left:0;z-index:99}footer{background-color:#21618c;color:#eee}footer #logo-text{text-align:left;display:flex;text-transform:uppercase;flex-direction:column}footer #logo-text .title{font-size:3em;letter-spacing:4px;line-height:0.8}footer #logo-text #blurb{font-size:1.2em;letter-spacing:1px;font-weight:300}footer .column h5{text-transform:uppercase;text-align:center;font-size:1.5em}footer .column p{margin-bottom:0}footer .column .mdi-set{display:flex;flex-direction:row;justify-content:center}footer .column .mdi-set a{font-size:3em;margin:0.5rem 1rem}#intro{text-align:center;padding:5rem;flex-direction:column}#project{background-color:#2e86c1;color:#eee}#project .text{align-self:flex-start;margin-right:5%}#robots{background-color:#85c1e9;color:#fff}#robots .title{text-align:center}#robots *{flex:50%}#donate{background-color:#2e86c1}#donate .left{color:#eee}#donate>*{flex:1 1 0}#donate .right{background-color:#eee;padding:2rem}#donate .right .title{text-align:center;margin-bottom:0}#donate .right #goal{font-weight:300}#donate .right #goal #raised{font-weight:600;font-size:1.5em}#donate .right #progress{position:relative;background-color:rgba(0,0,0,0.1);box-shadow:inset 0 1px 2px 1px rgba(0,0,0,0.1);margin:0.2rem 0 1rem}#donate .right #progress #meter{position:absolute;min-width:19%;display:block;background-color:#21618c;border-radius:1rem}#donate .right #progress,#donate .right #progress>#meter{height:1rem;border-radius:1rem}#donate .right .button{padding:1rem;margin-bottom:0}#donationpie{background-color:#85c1e9;color:#fff}#sponsors{display:flex;flex-direction:row;flex-wrap:wrap}#sponsors>*{flex:0 1 100%}#sponsors .title{text-align:center}#sponsors .row{display:flex;flex-direction:row;justify-content:space-around;align-items:center}#sponsors .row:not(:last-child){margin-bottom:2rem}#outreach>*{flex:1 1 0}#outreach img{width:100%}
